module.exports = {"markup":"<h1 id=\"dealing-with-update-blocking\"><a href=\"/core/guides/dealing-with-update-blocking\" class=\"internal-link\">Dealing with Update Blocking</a></h1>\n<p>React Router has a number of location-aware components that use the current <code>location</code> object to determine what they render. By default, the current <code>location</code> is passed implicitly to components using React&#x2019;s context model. When the location changes, those components should re-render using the new <code>location</code> object from the context.</p>\n<p>React provides two approaches to optimize the rendering performance of applications: the <code>shouldComponentUpdate</code> lifecycle method and the <code>PureComponent</code>. Both block the re-rendering of components unless the right conditions are met. Unfortunately, this means that React Router&#x2019;s location-aware components can become out of sync with the current location if their re-rendering was prevented.</p>\n<h3>Example of the Problem</h3>\n<p>We start out with a component that prevents updates.</p>\n<pre><code class=\"language-js\"><span class=\"token keyword\">class</span> <span class=\"token class-name\">UpdateBlocker</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">React<span class=\"token punctuation\">.</span>PureComponent</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">return</span> <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>props<span class=\"token punctuation\">.</span>children\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n<p>When the <code>&lt;UpdateBlocker&gt;</code> is mounting, any location-aware child components will use the current <code>location</code> and <code>match</code> objects to render.</p>\n<pre><code class=\"language-jsx\"><span class=\"token comment\">// location = { pathname: &apos;/about&apos; }</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>UpdateBlocker</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>NavLink</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/about<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">&gt;</span></span>About<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>NavLink</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token comment\">// &lt;a href=&apos;/about&apos; class=&apos;active&apos;&gt;About&lt;/a&gt;</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>NavLink</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/faq<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">&gt;</span></span>F<span class=\"token punctuation\">.</span>A<span class=\"token punctuation\">.</span>Q<span class=\"token punctuation\">.</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>NavLink</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token comment\">// &lt;a href=&apos;/faq&apos;&gt;F.A.Q.&lt;/a&gt;</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>UpdateBlocker</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<p>When the location changes, the <code>&lt;UpdateBlocker&gt;</code> does not detect any prop or state changes, so its child components will not be re-rendered.</p>\n<pre><code class=\"language-jsx\"><span class=\"token comment\">// location = { pathname: &apos;/faq&apos; }</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>UpdateBlocker</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token comment\">// the links will not re-render, so they retain their previous attributes</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>NavLink</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/about<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">&gt;</span></span>About<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>NavLink</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token comment\">// &lt;a href=&apos;/about&apos; class=&apos;active&apos;&gt;About&lt;/a&gt;</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>NavLink</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/faq<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">&gt;</span></span>F<span class=\"token punctuation\">.</span>A<span class=\"token punctuation\">.</span>Q<span class=\"token punctuation\">.</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>NavLink</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token comment\">// &lt;a href=&apos;/faq&apos;&gt;F.A.Q.&lt;/a&gt;</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>UpdateBlocker</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<h3><code>shouldComponentUpdate</code></h3>\n<p>In order for a component that implements <code>shouldComponentUpdate</code> to know that it <em>should</em> update when the location changes, its <code>shouldComponentUpdate</code> method needs to be able to detect location changes.</p>\n<p>If you are implementing <code>shouldComponentUpdate</code> yourself, you <em>could</em> compare the location from the current and next <code>context.router</code> objects. However, as a user, you should not have to use context directly. Instead, it would be ideal if you could compare the current and next <code>location</code> without touching the context.</p>\n<h4>Third-Party Code</h4>\n<p>You may run into issues with components not updating after a location change despite not calling <code>shouldComponentUpdate</code> yourself. This is most likely because <code>shouldComponentUpdate</code> is being called by third-party code, such as <code>react-redux</code>&apos;s <code>connect</code> and <code>mobx-react</code>&apos;s <code>observer</code>.</p>\n<pre><code class=\"language-js\"><span class=\"token comment\">// react-redux</span>\n<span class=\"token keyword\">const</span> MyConnectedComponent <span class=\"token operator\">=</span> <span class=\"token function\">connect</span><span class=\"token punctuation\">(</span>mapStateToProps<span class=\"token punctuation\">)</span><span class=\"token punctuation\">(</span>MyComponent<span class=\"token punctuation\">)</span>\n\n<span class=\"token comment\">// mobx-react</span>\n<span class=\"token keyword\">const</span> MyObservedComponent <span class=\"token operator\">=</span> <span class=\"token function\">observer</span><span class=\"token punctuation\">(</span>MyComponent<span class=\"token punctuation\">)</span>\n</code></pre>\n<p>With third-party code, you likely cannot even control the implementation of <code>shouldComponentUpdate</code>. Instead, you will have to structure your code to make location changes obvious to those methods.</p>\n<p>Both <code>connect</code> and <code>observer</code> create components whose <code>shouldComponentUpdate</code> methods do a shallow comparison of their current <code>props</code> and their next <code>props</code>. Those components will only re-render when at least one prop has changed. This means that in order to ensure they update when the location changes, they will need to be given a prop that changes when the location changes.</p>\n<h3><code>PureComponent</code></h3>\n<p>React&#x2019;s <code>PureComponent</code> does not implement <code>shouldComponentUpdate</code>, but it takes a similar approach to preventing updates. When a &#x201C;pure&#x201D; component updates, it will do a shallow comparison of its current <code>props</code> and <code>state</code> to the next <code>props</code> and <code>state</code>. If the comparison does not detect any differences, the component will not update. Like with <code>shouldComponentUpdate</code>, that means that in order to force a &#x201C;pure&#x201D; component to update when the location changes, it needs to have a prop or state that has changed.</p>\n<h3>The Solution</h3>\n<h4>Quick Solution</h4>\n<p>If you are running into this issue while using a higher-order component like <code>connect</code> (from react-redux) or <code>observer</code> (from Mobx), you can just wrap that component in a <code>withRouter</code> to remove the blocked updates.</p>\n<pre><code class=\"language-javascript\"><span class=\"token comment\">// redux before</span>\n<span class=\"token keyword\">const</span> MyConnectedComponent <span class=\"token operator\">=</span> <span class=\"token function\">connect</span><span class=\"token punctuation\">(</span>mapStateToProps<span class=\"token punctuation\">)</span><span class=\"token punctuation\">(</span>MyComponent<span class=\"token punctuation\">)</span>\n<span class=\"token comment\">// redux after</span>\n<span class=\"token keyword\">const</span> MyConnectedComponent <span class=\"token operator\">=</span> <span class=\"token function\">withRouter</span><span class=\"token punctuation\">(</span><span class=\"token function\">connect</span><span class=\"token punctuation\">(</span>mapStateToProps<span class=\"token punctuation\">)</span><span class=\"token punctuation\">(</span>MyComponent<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n\n<span class=\"token comment\">// mobx before</span>\n<span class=\"token keyword\">const</span> MyConnectedComponent <span class=\"token operator\">=</span> <span class=\"token function\">observer</span><span class=\"token punctuation\">(</span>MyComponent<span class=\"token punctuation\">)</span>\n<span class=\"token comment\">// mobx after</span>\n<span class=\"token keyword\">const</span> MyConnectedComponent <span class=\"token operator\">=</span> <span class=\"token function\">withRouter</span><span class=\"token punctuation\">(</span><span class=\"token function\">observer</span><span class=\"token punctuation\">(</span>MyComponent<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n</code></pre>\n<p><strong>This is not the most efficient solution</strong>, but will prevent the blocked updates issue. For more info regarding this solution, read the <a href=\"https://github.com/ReactTraining/react-router/blob/master/packages/react-router/docs/guides/redux.md#blocked-updates\">Redux guide</a>.  To understand why this is not the most optimal solution, read <a href=\"https://github.com/ReactTraining/react-router/pull/5552#issuecomment-331502281\">this thread</a>.</p>\n<h4>Recommended Solution</h4>\n<p>The key to avoiding blocked re-renders after location changes is to pass the blocking component the <code>location</code> object as a prop. This will be different whenever the location changes, so comparisons will detect that the current and next location are different.</p>\n<pre><code class=\"language-jsx\"><span class=\"token comment\">// location = { pathname: &apos;/about&apos; }</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>UpdateBlocker</span> <span class=\"token attr-name\">location</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>location<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>NavLink</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/about<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">&gt;</span></span>About<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>NavLink</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token comment\">// &lt;a href=&apos;/about&apos; class=&apos;active&apos;&gt;About&lt;/a&gt;</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>NavLink</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/faq<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">&gt;</span></span>F<span class=\"token punctuation\">.</span>A<span class=\"token punctuation\">.</span>Q<span class=\"token punctuation\">.</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>NavLink</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token comment\">// &lt;a href=&apos;/faq&apos;&gt;F.A.Q.&lt;/a&gt;</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>UpdateBlocker</span><span class=\"token punctuation\">&gt;</span></span>\n\n<span class=\"token comment\">// location = { pathname: &apos;/faq&apos; }</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>UpdateBlocker</span> <span class=\"token attr-name\">location</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>location<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>NavLink</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/about<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">&gt;</span></span>About<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>NavLink</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token comment\">// &lt;a href=&apos;/about&apos;&gt;About&lt;/a&gt;</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>NavLink</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/faq<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">&gt;</span></span>F<span class=\"token punctuation\">.</span>A<span class=\"token punctuation\">.</span>Q<span class=\"token punctuation\">.</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>NavLink</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token comment\">// &lt;a href=&apos;/faq&apos; class=&apos;active&apos;&gt;F.A.Q.&lt;/a&gt;</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>UpdateBlocker</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<h4>Getting the location</h4>\n<p>In order to pass the current <code>location</code> object as a prop to a component, you must have access to it. The primary way that a component can get access to the <code>location</code> is via a <code>&lt;Route&gt;</code> component. When a <code>&lt;Route&gt;</code> matches (or always if you are using the <code>children</code> prop), it passes the current <code>location</code> to the child element it renders.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/here<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Here<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token keyword\">const</span> <span class=\"token function-variable function\">Here</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span>props<span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// props.location = { pathname: &apos;/here&apos;, ... }</span>\n  <span class=\"token keyword\">return</span> <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>You are here<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token operator\">&lt;</span>Route path<span class=\"token operator\">=</span><span class=\"token string\">&apos;/there&apos;</span> render<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">(</span>props<span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// props.location = { pathname: &apos;/there&apos;, ... }</span>\n  <span class=\"token keyword\">return</span> <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>You are there<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span><span class=\"token operator\">/</span><span class=\"token operator\">&gt;</span>\n\n<span class=\"token operator\">&lt;</span>Route path<span class=\"token operator\">=</span><span class=\"token string\">&apos;/everywhere&apos;</span> children<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">(</span>props<span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// props.location = { pathname: &apos;/everywhere&apos;, ... }</span>\n  <span class=\"token keyword\">return</span> <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>You are everywhere<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span><span class=\"token operator\">/</span><span class=\"token operator\">&gt;</span>\n</code></pre>\n<p>This means that given a component that blocks updates, you can easily pass it the <code>location</code> as a prop in the following ways:</p>\n<pre><code class=\"language-jsx\"><span class=\"token comment\">// the Blocker is a &quot;pure&quot; component, so it will only</span>\n<span class=\"token comment\">// update when it receives new props</span>\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">Blocker</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">React<span class=\"token punctuation\">.</span>PureComponent</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>NavLink</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/oz<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">&gt;</span></span>Oz<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>NavLink</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>NavLink</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/kansas<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">&gt;</span></span>Kansas<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>NavLink</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n<ol>\n<li>A component rendered directly by a <code>&lt;Route&gt;</code> does not have to worry about blocked updates because it has the <code>location</code> injected as a prop.</li>\n</ol>\n<pre><code class=\"language-jsx\"><span class=\"token comment\">// The &lt;Blocker&gt;&apos;s location prop will change whenever</span>\n<span class=\"token comment\">// the location changes</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/:place<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Blocker<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<ol start=\"2\">\n<li>A component rendered directly by a <code>&lt;Route&gt;</code> can pass that location prop to any child elements it creates.</li>\n</ol>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/parent<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Parent<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/&gt;</span></span>\n\n<span class=\"token keyword\">const</span> <span class=\"token function-variable function\">Parent</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span>props<span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// &lt;Parent&gt; receives the location as a prop. Any child</span>\n  <span class=\"token comment\">// element it creates can be passed the location.</span>\n  <span class=\"token keyword\">return</span> <span class=\"token punctuation\">(</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>SomeComponent</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Blocker</span> <span class=\"token attr-name\">location</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>props<span class=\"token punctuation\">.</span>location<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>SomeComponent</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n<p>What happens when the component isn&#x2019;t being rendered by a <code>&lt;Route&gt;</code> and the component rendering it does not have the <code>location</code> in its variable scope? There are two approaches that you can take to automatically inject the <code>location</code> as a prop of your component.</p>\n<ol>\n<li>Render a pathless <code>&lt;Route&gt;</code>. While <code>&lt;Route&gt;</code>s are typically used for matching a specific path, a pathless <code>&lt;Route&gt;</code> will always match, so it will always render its component.</li>\n</ol>\n<pre><code class=\"language-jsx\"><span class=\"token comment\">// pathless &lt;Route&gt; = &lt;Blocker&gt; will always be rendered</span>\n<span class=\"token keyword\">const</span> <span class=\"token function-variable function\">MyComponent</span><span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>SomeComponent</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Blocker<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>SomeComponent</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">)</span>\n</code></pre>\n<ol start=\"2\">\n<li>You can wrap a component with the <code>withRouter</code> higher-order component and it will be given the current <code>location</code> as one of its props.</li>\n</ol>\n<pre><code class=\"language-jsx\"><span class=\"token comment\">// internally, withRouter just renders a pathless &lt;Route&gt;</span>\n<span class=\"token keyword\">const</span> BlockAvoider <span class=\"token operator\">=</span> <span class=\"token function\">withRouter</span><span class=\"token punctuation\">(</span>Blocker<span class=\"token punctuation\">)</span>\n\n<span class=\"token keyword\">const</span> <span class=\"token function-variable function\">MyComponent</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>SomeComponent</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>BlockAvoider</span> <span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>SomeComponent</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">)</span>\n</code></pre>\n","headers":[],"title":{"text":"Dealing with Update Blocking","slug":"dealing-with-update-blocking"}}

