module.exports = {"markup":"<h1 id=\"static-routes\"><a href=\"/core/guides/static-routes\" class=\"internal-link\">Static Routes</a></h1>\n<p>Previous versions of React Router used static routes to configure your application&#x2019;s routing. This allows for inspection and matching of routes before rendering.  Since v4 moved to dynamic components instead of route configs, some previous use-cases became less obvious and tricky.</p>\n<p>We are developing a package to work with static route configs and React Router, to continue to meet those use-cases. It is under development now but we&#x2019;d love for you to try it out and help out.</p>\n<p><a href=\"https://github.com/reacttraining/react-router/tree/master/packages/react-router-config\">React Router Config</a></p>\n","headers":[],"title":{"text":"Static Routes","slug":"static-routes"}}

