module.exports = {"markup":"<h1 id=\"Link\"><a href=\"/web/api/Link\" class=\"internal-link\">&lt;Link&gt;</a></h1>\n<p>Provides declarative, accessible navigation around your application.</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> Link <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-router-dom&apos;</span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Link</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/about<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">&gt;</span></span>About<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Link</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<h2 id=\"Link-to-string\"><a href=\"/web/api/Link/to-string\" class=\"internal-link\">to: string</a></h2>\n<p>A string representation of the location to link to, created by concatenating the location&#x2019;s pathname, search, and hash properties.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Link</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/courses?sort=name<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<h2 id=\"Link-to-object\"><a href=\"/web/api/Link/to-object\" class=\"internal-link\">to: object</a></h2>\n<p>An object that can have any of the following properties:</p>\n<ul>\n<li><code>pathname</code>: A string representing the path to link to.</li>\n<li><code>search</code>: A string represenation of query parameters.</li>\n<li><code>hash</code>: A hash to put in the URL, e.g. <code>#a-hash</code>.</li>\n<li><code>state</code>: State to persist to the <code>location</code>.</li>\n</ul>\n<pre><code class=\"language-jsx\"><span class=\"token operator\">&lt;</span>Link to<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span>\n  pathname<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;/courses&apos;</span><span class=\"token punctuation\">,</span>\n  search<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;?sort=name&apos;</span><span class=\"token punctuation\">,</span>\n  hash<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;#the-hash&apos;</span><span class=\"token punctuation\">,</span>\n  state<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span> fromDashboard<span class=\"token punctuation\">:</span> <span class=\"token boolean\">true</span> <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span><span class=\"token operator\">/</span><span class=\"token operator\">&gt;</span>\n</code></pre>\n<h2 id=\"Link-replace-bool\"><a href=\"/web/api/Link/replace-bool\" class=\"internal-link\">replace: bool</a></h2>\n<p>When <code>true</code>, clicking the link will replace the current entry in the history stack instead of adding a new one.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Link</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/courses<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">replace</span> <span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<h2 id=\"Link-innerref-function\"><a href=\"/web/api/Link/innerref-function\" class=\"internal-link\">innerRef: function</a></h2>\n<p>Allows access to the underlying <code>ref</code> of the component</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">const</span> <span class=\"token function-variable function\">refCallback</span> <span class=\"token operator\">=</span> node <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// `node` refers to the mounted DOM element or null when unmounted</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Link</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">innerRef</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>refCallback<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<h2 id=\"Link-others\"><a href=\"/web/api/Link/others\" class=\"internal-link\">others</a></h2>\n<p>You can also pass props you&#x2019;d like to be on the <code>&lt;a&gt;</code> such as a <code>title</code>, <code>id</code>, <code>className</code>, etc.</p>\n","headers":[{"text":"to: string","slug":"to-string"},{"text":"to: object","slug":"to-object"},{"text":"replace: bool","slug":"replace-bool"},{"text":"innerRef: function","slug":"innerref-function"},{"text":"others","slug":"others"}],"title":{"text":"<Link>","slug":"Link"}}

