module.exports = {"markup":"<h1 id=\"Route\"><a href=\"/web/api/Route\" class=\"internal-link\">&lt;Route&gt;</a></h1>\n<p>The Route component is perhaps the most important component in React\nRouter to understand and learn to use well. Its most basic\nresponsibility is to render some UI when a <a href=\"/web/api/location\" class=\"internal-link\">location</a>\nmatches the route&#x2019;s <code>path</code>.</p>\n<p>Consider the following code:</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> BrowserRouter <span class=\"token keyword\">as</span> Router<span class=\"token punctuation\">,</span> Route <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-router-dom&apos;</span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Router</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">exact</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Home<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/news<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>NewsFeed<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Router</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<p>If the location of the app is <code>/</code> then the UI hierarchy will be something like:</p>\n<pre><code class=\"language-html\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Home</span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token comment\">&lt;!-- react-empty: 2 --&gt;</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<p>And if the location of the app is <code>/news</code> then the UI hierarchy will be:</p>\n<pre><code class=\"language-html\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token comment\">&lt;!-- react-empty: 1 --&gt;</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>NewsFeed</span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<p>The &#x201C;react-empty&#x201D; comments are just implementation details of React&#x2019;s <code>null</code> rendering. But for our purposes, it is instructive. A Route is always technically &#x201C;rendered&#x201D; even though its rendering <code>null</code>. As soon as the app location matches the route&#x2019;s path, your component will be rendered.</p>\n<h2 id=\"Route-route-render-methods\"><a href=\"/web/api/Route/route-render-methods\" class=\"internal-link\">Route render methods</a></h2>\n<p>There are 3 ways to render something with a <code>&lt;Route&gt;</code>:</p>\n<ul>\n<li><a href=\"/web/api/Route/component\" class=\"internal-link\"><code>&lt;Route component&gt;</code></a></li>\n<li><a href=\"/web/api/Route/render-func\" class=\"internal-link\"><code>&lt;Route render&gt;</code></a></li>\n<li><a href=\"/web/api/Route/children-func\" class=\"internal-link\"><code>&lt;Route children&gt;</code></a></li>\n</ul>\n<p>Each is useful in different circumstances. You should use only one of these props on a given <code>&lt;Route&gt;</code>. See their explanations below to understand why you have 3 options. Most of the time you&#x2019;ll use <code>component</code>.</p>\n<h2 id=\"Route-route-props\"><a href=\"/web/api/Route/route-props\" class=\"internal-link\">Route props</a></h2>\n<p>All three <a href=\"/web/api/Route/route-render-methods\" class=\"internal-link\">render methods</a> will be passed the same three route props</p>\n<ul>\n<li><a href=\"/web/api/match\" class=\"internal-link\">match</a></li>\n<li><a href=\"/web/api/location\" class=\"internal-link\">location</a></li>\n<li><a href=\"/web/api/history\" class=\"internal-link\">history</a></li>\n</ul>\n<h2 id=\"Route-component\"><a href=\"/web/api/Route/component\" class=\"internal-link\">component</a></h2>\n<p>A React component to render only when the location matches. It will be\nrendered with <a href=\"/web/api/Route/route-props\" class=\"internal-link\">route props</a>.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/user/:username<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>User<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n\n<span class=\"token keyword\">const</span> <span class=\"token function-variable function\">User</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span> match <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">return</span> <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>h1</span><span class=\"token punctuation\">&gt;</span></span>Hello <span class=\"token punctuation\">{</span>match<span class=\"token punctuation\">.</span>params<span class=\"token punctuation\">.</span>username<span class=\"token punctuation\">}</span><span class=\"token operator\">!</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>h1</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n<p>When you use <code>component</code> (instead of <code>render</code> or <code>children</code>, below) the router uses <a href=\"https://facebook.github.io/react/docs/react-api.html#createelement\"><code>React.createElement</code></a> to create a new <a href=\"https://facebook.github.io/react/docs/rendering-elements.html\">React element</a> from the given component. That means if you provide an inline function to the <code>component</code> prop, you would create a new component every render. This results in the existing component unmounting and the new component mounting instead of just updating the existing component. When using an inline function for inline rendering, use the <code>render</code> or the <code>children</code> prop (below).</p>\n<h2 id=\"Route-render-func\"><a href=\"/web/api/Route/render-func\" class=\"internal-link\">render: func</a></h2>\n<p>This allows for convenient inline rendering and wrapping without the undesired remounting explained above.</p>\n<p>Instead of having a new <a href=\"https://facebook.github.io/react/docs/rendering-elements.html\">React element</a> created for you using the <a href=\"/web/api/Route/component\" class=\"internal-link\"><code>component</code></a> prop, you can pass in a function to be called when the location matches. The <code>render</code> prop receives all the same <a href=\"/web/api/Route/route-props\" class=\"internal-link\">route props</a> as the <code>component</code> render prop.</p>\n<pre><code class=\"language-jsx\"><span class=\"token comment\">// convenient inline rendering</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/home<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">render</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>Home<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n\n<span class=\"token comment\">// wrapping/composing</span>\n<span class=\"token keyword\">const</span> <span class=\"token function-variable function\">FadingRoute</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span> component<span class=\"token punctuation\">:</span> Component<span class=\"token punctuation\">,</span> <span class=\"token operator\">...</span>rest <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token spread\"><span class=\"token punctuation\">{</span><span class=\"token punctuation\">...</span><span class=\"token attr-value\">rest</span><span class=\"token punctuation\">}</span></span> <span class=\"token attr-name\">render</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>props <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>FadeIn</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token operator\">&lt;</span>Component <span class=\"token punctuation\">{</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span>props<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>FadeIn</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token punctuation\">)</span><span class=\"token punctuation\">}</span><span class=\"token operator\">/</span><span class=\"token operator\">&gt;</span>\n<span class=\"token punctuation\">)</span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>FadingRoute</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/cool<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Something<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<p><strong>Warning:</strong> <code>&lt;Route component&gt;</code> takes precedence over <code>&lt;Route render&gt;</code> so don&#x2019;t use both in the same <code>&lt;Route&gt;</code>.</p>\n<h2 id=\"Route-children-func\"><a href=\"/web/api/Route/children-func\" class=\"internal-link\">children: func</a></h2>\n<p>Sometimes you need to render whether the path matches the location or not. In these cases, you can use the function <code>children</code> prop. It works exactly like <code>render</code> except that it gets called whether there is a match or not.</p>\n<p>The <code>children</code> render prop receives all the same <a href=\"/web/api/Route/route-props\" class=\"internal-link\">route props</a> as the <code>component</code> and <code>render</code> methods, except when a route fails to match the URL, then <code>match</code> is <code>null</code>. This allows you to dynamically adjust your UI based on whether or not the route matches. Here we&#x2019;re adding an <code>active</code> class if the route matches</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>ul</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>ListItemLink</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/somewhere<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>ListItemLink</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/somewhere-else<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>ul</span><span class=\"token punctuation\">&gt;</span></span>\n\n<span class=\"token keyword\">const</span> <span class=\"token function-variable function\">ListItemLink</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span> to<span class=\"token punctuation\">,</span> <span class=\"token operator\">...</span>rest <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n  <span class=\"token operator\">&lt;</span>Route path<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span>to<span class=\"token punctuation\">}</span> children<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span> match <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>li</span> <span class=\"token attr-name\">className</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>match <span class=\"token operator\">?</span> <span class=\"token string\">&apos;active&apos;</span> <span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;&apos;</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Link</span> <span class=\"token attr-name\">to</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>to<span class=\"token punctuation\">}</span></span> <span class=\"token spread\"><span class=\"token punctuation\">{</span><span class=\"token punctuation\">...</span><span class=\"token attr-value\">rest</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>li</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token punctuation\">)</span><span class=\"token punctuation\">}</span><span class=\"token operator\">/</span><span class=\"token operator\">&gt;</span>\n<span class=\"token punctuation\">)</span>\n</code></pre>\n<p>This could also be useful for animations:</p>\n<pre><code class=\"language-jsx\"><span class=\"token operator\">&lt;</span>Route children<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span> match<span class=\"token punctuation\">,</span> <span class=\"token operator\">...</span>rest <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n  <span class=\"token punctuation\">{</span><span class=\"token comment\">/* Animate will always render, so you can use lifecycles\n      to animate its child in and out */</span><span class=\"token punctuation\">}</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Animate</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token punctuation\">{</span>match <span class=\"token operator\">&amp;&amp;</span> <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Something</span> <span class=\"token spread\"><span class=\"token punctuation\">{</span><span class=\"token punctuation\">...</span><span class=\"token attr-value\">rest</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span><span class=\"token punctuation\">}</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Animate</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">)</span><span class=\"token punctuation\">}</span><span class=\"token operator\">/</span><span class=\"token operator\">&gt;</span>\n</code></pre>\n<p><strong>Warning:</strong> Both <code>&lt;Route component&gt;</code> and <code>&lt;Route render&gt;</code> take precedence over <code>&lt;Route children&gt;</code> so don&#x2019;t use more than one in the same <code>&lt;Route&gt;</code>.</p>\n<h2 id=\"Route-path-string\"><a href=\"/web/api/Route/path-string\" class=\"internal-link\">path: string</a></h2>\n<p>Any valid URL path that <a href=\"https://www.npmjs.com/package/path-to-regexp\"><code>path-to-regexp</code></a> understands.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/users/:id<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>User<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<p>Routes without a <code>path</code> <em>always</em> match.</p>\n<h2 id=\"Route-exact-bool\"><a href=\"/web/api/Route/exact-bool\" class=\"internal-link\">exact: bool</a></h2>\n<p>When <code>true</code>, will only match if the path matches the <code>location.pathname</code> <em>exactly</em>.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">exact</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/one<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>About<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<table>\n<thead>\n<tr>\n<th>path</th>\n<th>location.pathname</th>\n<th>exact</th>\n<th>matches?</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>/one</code></td>\n<td><code>/one/two</code></td>\n<td><code>true</code></td>\n<td>no</td>\n</tr>\n<tr>\n<td><code>/one</code></td>\n<td><code>/one/two</code></td>\n<td><code>false</code></td>\n<td>yes</td>\n</tr>\n</tbody>\n</table>\n<h2 id=\"Route-strict-bool\"><a href=\"/web/api/Route/strict-bool\" class=\"internal-link\">strict: bool</a></h2>\n<p>When <code>true</code>, a <code>path</code> that has a trailing slash will only match a <code>location.pathname</code> with a trailing slash. This has no effect when there are additional URL segments in the <code>location.pathname</code>.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">strict</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/one/<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>About<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<table>\n<thead>\n<tr>\n<th>path</th>\n<th>location.pathname</th>\n<th>matches?</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>/one/</code></td>\n<td><code>/one</code></td>\n<td>no</td>\n</tr>\n<tr>\n<td><code>/one/</code></td>\n<td><code>/one/</code></td>\n<td>yes</td>\n</tr>\n<tr>\n<td><code>/one/</code></td>\n<td><code>/one/two</code></td>\n<td>yes</td>\n</tr>\n</tbody>\n</table>\n<p><strong>Warning:</strong> <code>strict</code> can be used to enforce that a <code>location.pathname</code> has no trailing slash, but in order to do this both <code>strict</code> and <code>exact</code> must be <code>true</code>.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">exact</span> <span class=\"token attr-name\">strict</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/one<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>About<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<table>\n<thead>\n<tr>\n<th>path</th>\n<th>location.pathname</th>\n<th>matches?</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>/one</code></td>\n<td><code>/one</code></td>\n<td>yes</td>\n</tr>\n<tr>\n<td><code>/one</code></td>\n<td><code>/one/</code></td>\n<td>no</td>\n</tr>\n<tr>\n<td><code>/one</code></td>\n<td><code>/one/two</code></td>\n<td>no</td>\n</tr>\n</tbody>\n</table>\n<h2 id=\"Route-location-object\"><a href=\"/web/api/Route/location-object\" class=\"internal-link\">location: object</a></h2>\n<p>A <code>&lt;Route&gt;</code> element tries to match its <code>path</code> to the current history location (usually the current browser URL).\nHowever, a <a href=\"location.md\"><code>location</code></a> with a different <code>pathname</code> can also be passed for matching.</p>\n<p>This is useful in cases when you need to match a <code>&lt;Route&gt;</code> to a location other than the current history location, as shown in the <a href=\"https://reacttraining.com/react-router/web/example/animated-transitions\">Animated Transitions</a> example.</p>\n<p>If a <code>&lt;Route&gt;</code> element is wrapped in a <code>&lt;Switch&gt;</code> and matches the location passed to the <code>&lt;Switch&gt;</code> (or the current history location), then the <code>location</code> prop passed to <code>&lt;Route&gt;</code> will be overridden by the one used by the <code>&lt;Switch&gt;</code> (given <a href=\"https://github.com/ReactTraining/react-router/blob/master/packages/react-router/modules/Switch.js#L51\">here</a>).</p>\n<h2 id=\"Route-sensitive-bool\"><a href=\"/web/api/Route/sensitive-bool\" class=\"internal-link\">sensitive: bool</a></h2>\n<p>When <code>true</code>, will match if the path is <strong>case sensitive</strong>.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">sensitive</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/one<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>About<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<table>\n<thead>\n<tr>\n<th>path</th>\n<th>location.pathname</th>\n<th>sensitive</th>\n<th>matches?</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>/one</code></td>\n<td><code>/one</code></td>\n<td><code>true</code></td>\n<td>yes</td>\n</tr>\n<tr>\n<td><code>/One</code></td>\n<td><code>/one</code></td>\n<td><code>true</code></td>\n<td>no</td>\n</tr>\n<tr>\n<td><code>/One</code></td>\n<td><code>/one</code></td>\n<td><code>false</code></td>\n<td>yes</td>\n</tr>\n</tbody>\n</table>\n","headers":[{"text":"Route render methods","slug":"route-render-methods"},{"text":"Route props","slug":"route-props"},{"text":"component","slug":"component"},{"text":"render: func","slug":"render-func"},{"text":"children: func","slug":"children-func"},{"text":"path: string","slug":"path-string"},{"text":"exact: bool","slug":"exact-bool"},{"text":"strict: bool","slug":"strict-bool"},{"text":"location: object","slug":"location-object"},{"text":"sensitive: bool","slug":"sensitive-bool"}],"title":{"text":"<Route>","slug":"Route"}}

