module.exports = {"markup":"<h1 id=\"Router\"><a href=\"/web/api/Router\" class=\"internal-link\">&lt;Router&gt;</a></h1>\n<p>The common low-level interface for all router components. Typically apps will use one of the high-level routers instead:</p>\n<ul>\n<li><a href=\"/web/api/BrowserRouter\" class=\"internal-link\"><code>&lt;BrowserRouter&gt;</code></a></li>\n<li><a href=\"/web/api/HashRouter\" class=\"internal-link\"><code>&lt;HashRouter&gt;</code></a></li>\n<li><a href=\"/web/api/MemoryRouter\" class=\"internal-link\"><code>&lt;MemoryRouter&gt;</code></a></li>\n<li><a href=\"/native/api/NativeRouter\" class=\"internal-link\"><code>&lt;NativeRouter&gt;</code></a></li>\n<li><a href=\"/web/api/StaticRouter\" class=\"internal-link\"><code>&lt;StaticRouter&gt;</code></a></li>\n</ul>\n<p>The most common use-case for using the low-level <code>&lt;Router&gt;</code> is to\nsynchronize a custom history with a state management lib like Redux or Mobx. Note that this is not required to use state management libs alongside React Router, it&#x2019;s only for deep integration.</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> Router <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-router&apos;</span>\n<span class=\"token keyword\">import</span> createBrowserHistory <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;history/createBrowserHistory&apos;</span>\n\n<span class=\"token keyword\">const</span> history <span class=\"token operator\">=</span> <span class=\"token function\">createBrowserHistory</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Router</span> <span class=\"token attr-name\">history</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>history<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>App</span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Router</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<h2 id=\"Router-history-object\"><a href=\"/web/api/Router/history-object\" class=\"internal-link\">history: object</a></h2>\n<p>A <a href=\"https://github.com/ReactTraining/history\"><code>history</code></a> object to use for navigation.</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">import</span> createBrowserHistory <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;history/createBrowserHistory&apos;</span>\n\n<span class=\"token keyword\">const</span> customHistory <span class=\"token operator\">=</span> <span class=\"token function\">createBrowserHistory</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Router</span> <span class=\"token attr-name\">history</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>customHistory<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<h2 id=\"Router-children-node\"><a href=\"/web/api/Router/children-node\" class=\"internal-link\">children: node</a></h2>\n<p>A <a href=\"https://facebook.github.io/react/docs/react-api.html#react.children.only\">single child element</a> to render.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Router</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>App</span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Router</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n","headers":[{"text":"history: object","slug":"history-object"},{"text":"children: node","slug":"children-node"}],"title":{"text":"<Router>","slug":"Router"}}

