module.exports = {"markup":"<h1 id=\"Switch\"><a href=\"/web/api/Switch\" class=\"internal-link\">&lt;Switch&gt;</a></h1>\n<p>Renders the first child <a href=\"Route.md\"><code>&lt;Route&gt;</code></a> or <a href=\"Redirect.md\"><code>&lt;Redirect&gt;</code></a> that matches the location.</p>\n<p><strong>How is this different than just using a bunch of <code>&lt;Route&gt;</code>s?</strong></p>\n<p><code>&lt;Switch&gt;</code> is unique in that it renders a route <em>exclusively</em>. In contrast, every <code>&lt;Route&gt;</code> that matches the location renders <em>inclusively</em>. Consider this code:</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/about<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>About<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/:user<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>User<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>NoMatch<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<p>If the URL is <code>/about</code>, then <code>&lt;About&gt;</code>, <code>&lt;User&gt;</code>, and <code>&lt;NoMatch&gt;</code> will all render because they all match the path. This is by design, allowing us to compose <code>&lt;Route&gt;</code>s into our apps in many ways, like sidebars and breadcrumbs, bootstrap tabs, etc.</p>\n<p>Occasionally, however, we want to pick only one <code>&lt;Route&gt;</code> to render. If we&#x2019;re at <code>/about</code> we don&#x2019;t want to also match <code>/:user</code> (or show our &#x201C;404&#x201D; page). Here&#x2019;s how to do it with <code>Switch</code>:</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> Switch<span class=\"token punctuation\">,</span> Route <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-router&apos;</span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">exact</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Home<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/about<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>About<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/:user<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>User<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>NoMatch<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<p>Now, if we&#x2019;re at <code>/about</code>, <code>&lt;Switch&gt;</code> will start looking for a matching <code>&lt;Route&gt;</code>. <code>&lt;Route path=&quot;/about&quot;/&gt;</code> will match and <code>&lt;Switch&gt;</code> will stop looking for matches and render <code>&lt;About&gt;</code>. Similarly, if we&#x2019;re at <code>/michael</code> then <code>&lt;User&gt;</code> will render.</p>\n<p>This is also useful for animated transitions since the matched <code>&lt;Route&gt;</code> is rendered in the same position as the previous one.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Fade</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token punctuation\">{</span><span class=\"token comment\">/* there will only ever be one child here */</span><span class=\"token punctuation\">}</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span><span class=\"token punctuation\">/&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Fade</span><span class=\"token punctuation\">&gt;</span></span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Fade</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token punctuation\">{</span><span class=\"token comment\">/* there will always be two children here,\n      one might render null though, making transitions\n      a bit more cumbersome to work out */</span><span class=\"token punctuation\">}</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Fade</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<h2 id=\"Switch-location-object\"><a href=\"/web/api/Switch/location-object\" class=\"internal-link\">location: object</a></h2>\n<p>A <a href=\"/web/api/location\" class=\"internal-link\"><code>location</code></a> object to be used for matching children elements instead of the current history location (usually the current browser URL).</p>\n<h2 id=\"Switch-children-node\"><a href=\"/web/api/Switch/children-node\" class=\"internal-link\">children: node</a></h2>\n<p>All children of a <code>&lt;Switch&gt;</code> should be <code>&lt;Route&gt;</code> or <code>&lt;Redirect&gt;</code> elements. Only the first child to match the current location will be rendered.</p>\n<p><code>&lt;Route&gt;</code> elements are matched using their <code>path</code> prop and <code>&lt;Redirect&gt;</code> elements are matched using their <code>from</code> prop. A <code>&lt;Route&gt;</code> with no <code>path</code> prop or a <code>&lt;Redirect&gt;</code> with no <code>from</code> prop will always match the current location.</p>\n<p>When you include a <code>&lt;Redirect&gt;</code> in a <code>&lt;Switch&gt;</code>, it can use any of the <code>&lt;Route&gt;</code>&apos;s location matching props: <code>path</code>, <code>exact</code>, and <code>strict</code>. <code>from</code> is just an alias for the <code>path</code> prop.</p>\n<p>If a <code>location</code> prop is given to the <code>&lt;Switch&gt;</code>, it will override the <code>location</code> prop on the matching child element.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">exact</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Home<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/users<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Users<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Redirect</span> <span class=\"token attr-name\">from</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/accounts<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/users<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">/&gt;</span></span>\n\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>NoMatch<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n","headers":[{"text":"location: object","slug":"location-object"},{"text":"children: node","slug":"children-node"}],"title":{"text":"<Switch>","slug":"Switch"}}

