module.exports = {"markup":"<h1 id=\"history\"><a href=\"/web/api/history\" class=\"internal-link\">history</a></h1>\n<p>The term &#x201C;history&#x201D; and &quot;<code>history</code> object&quot; in this documentation refers to <a href=\"https://github.com/ReactTraining/history\">the <code>history</code> package</a>, which is one of only 2 major dependencies of React Router (besides React itself), and which provides several different implementations for managing session history in JavaScript in various environments.</p>\n<p>The following terms are also used:</p>\n<ul>\n<li>&#x201C;browser history&#x201D; - A DOM-specific implementation, useful in web browsers that support the HTML5 history API</li>\n<li>&#x201C;hash history&#x201D; - A DOM-specific implementation for legacy web browsers</li>\n<li>&#x201C;memory history&#x201D; - An in-memory history implementation, useful in testing and non-DOM environments like React Native</li>\n</ul>\n<p><code>history</code> objects typically have the following properties and methods:</p>\n<ul>\n<li><code>length</code> - (number) The number of entries in the history stack</li>\n<li><code>action</code> - (string) The current action (<code>PUSH</code>, <code>REPLACE</code>, or <code>POP</code>)</li>\n<li><code>location</code> - (object) The current location. May have the following properties:\n<ul>\n<li><code>pathname</code> - (string) The path of the URL</li>\n<li><code>search</code> - (string) The URL query string</li>\n<li><code>hash</code> - (string) The URL hash fragment</li>\n<li><code>state</code> - (object) location-specific state that was provided to e.g. <code>push(path, state)</code> when this location was pushed onto the stack. Only available in browser and memory history.</li>\n</ul>\n</li>\n<li><code>push(path, [state])</code> - (function) Pushes a new entry onto the history stack</li>\n<li><code>replace(path, [state])</code> - (function) Replaces the current entry on the history stack</li>\n<li><code>go(n)</code> - (function) Moves the pointer in the history stack by <code>n</code> entries</li>\n<li><code>goBack()</code> - (function) Equivalent to <code>go(-1)</code></li>\n<li><code>goForward()</code> - (function) Equivalent to <code>go(1)</code></li>\n<li><code>block(prompt)</code> - (function) Prevents navigation (see <a href=\"https://github.com/ReactTraining/history#blocking-transitions\">the history docs</a>)</li>\n</ul>\n<h2 id=\"history-history-is-mutable\"><a href=\"/web/api/history/history-is-mutable\" class=\"internal-link\">history is mutable</a></h2>\n<p>The history object is mutable. Therefore it is recommended to access the <a href=\"/web/api/location\" class=\"internal-link\"><code>location</code></a> from the render props of <a href=\"/web/api/Route\" class=\"internal-link\"><code>&lt;Route&gt;</code></a>, not from <code>history.location</code>. This ensures your assumptions about React are correct in lifecycle hooks. For example:</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">class</span> <span class=\"token class-name\">Comp</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">React<span class=\"token punctuation\">.</span>Component</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">componentWillReceiveProps</span><span class=\"token punctuation\">(</span>nextProps<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token comment\">// will be true</span>\n    <span class=\"token keyword\">const</span> locationChanged <span class=\"token operator\">=</span> nextProps<span class=\"token punctuation\">.</span>location <span class=\"token operator\">!==</span> <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>props<span class=\"token punctuation\">.</span>location\n\n    <span class=\"token comment\">// INCORRECT, will *always* be false because history is mutable.</span>\n    <span class=\"token keyword\">const</span> locationChanged <span class=\"token operator\">=</span> nextProps<span class=\"token punctuation\">.</span>history<span class=\"token punctuation\">.</span>location <span class=\"token operator\">!==</span> <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>props<span class=\"token punctuation\">.</span>history<span class=\"token punctuation\">.</span>location\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Comp<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<p>Additional properties may also be present depending on the implementation you&#x2019;re using. Please refer to <a href=\"https://github.com/ReactTraining/history#properties\">the history documentation</a> for more details.</p>\n","headers":[{"text":"history is mutable","slug":"history-is-mutable"}],"title":{"text":"history","slug":"history"}}

