module.exports = {"markup":"<h1 id=\"withRouter\"><a href=\"/web/api/withRouter\" class=\"internal-link\">withRouter</a></h1>\n<p>You can get access to the <a href=\"/web/api/history\" class=\"internal-link\"><code>history</code></a> object&#x2019;s properties and the closest <a href=\"/web/api/Route\" class=\"internal-link\"><code>&lt;Route&gt;</code></a>&apos;s <a href=\"/web/api/match\" class=\"internal-link\"><code>match</code></a> via the <code>withRouter</code> higher-order component. <code>withRouter</code> will pass updated <code>match</code>, <code>location</code>, and <code>history</code> props to the wrapped component whenever it renders.</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">import</span> React <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react&apos;</span>\n<span class=\"token keyword\">import</span> PropTypes <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;prop-types&apos;</span>\n<span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> withRouter <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-router&apos;</span>\n\n<span class=\"token comment\">// A simple component that shows the pathname of the current location</span>\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">ShowTheLocation</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">React<span class=\"token punctuation\">.</span>Component</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">static</span> propTypes <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n    match<span class=\"token punctuation\">:</span> PropTypes<span class=\"token punctuation\">.</span>object<span class=\"token punctuation\">.</span>isRequired<span class=\"token punctuation\">,</span>\n    location<span class=\"token punctuation\">:</span> PropTypes<span class=\"token punctuation\">.</span>object<span class=\"token punctuation\">.</span>isRequired<span class=\"token punctuation\">,</span>\n    history<span class=\"token punctuation\">:</span> PropTypes<span class=\"token punctuation\">.</span>object<span class=\"token punctuation\">.</span>isRequired\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">const</span> <span class=\"token punctuation\">{</span> match<span class=\"token punctuation\">,</span> location<span class=\"token punctuation\">,</span> history <span class=\"token punctuation\">}</span> <span class=\"token operator\">=</span> <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>props\n\n    <span class=\"token keyword\">return</span> <span class=\"token punctuation\">(</span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>You are now at <span class=\"token punctuation\">{</span>location<span class=\"token punctuation\">.</span>pathname<span class=\"token punctuation\">}</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token comment\">// Create a new component that is &quot;connected&quot; (to borrow redux</span>\n<span class=\"token comment\">// terminology) to the router.</span>\n<span class=\"token keyword\">const</span> ShowTheLocationWithRouter <span class=\"token operator\">=</span> <span class=\"token function\">withRouter</span><span class=\"token punctuation\">(</span>ShowTheLocation<span class=\"token punctuation\">)</span>\n</code></pre>\n<h4>Important Note</h4>\n<p><code>withRouter</code> does not subscribe to location changes like React Redux&#x2019;s <code>connect</code> does for state changes.  Instead, re-renders after location changes propagate out from the <code>&lt;Router&gt;</code> component.  This means that <code>withRouter</code> does <em>not</em> re-render on route transitions unless its parent component re-renders. If you are using <code>withRouter</code> to prevent updates from being blocked by <code>shouldComponentUpdate</code>, it is important that <code>withRouter</code> wraps the component that implements <code>shouldComponentUpdate</code>. For example, when using Redux:</p>\n<pre><code class=\"language-js\"><span class=\"token comment\">// This gets around shouldComponentUpdate</span>\n<span class=\"token function\">withRouter</span><span class=\"token punctuation\">(</span><span class=\"token function\">connect</span><span class=\"token punctuation\">(</span><span class=\"token operator\">...</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">(</span>MyComponent<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n<span class=\"token comment\">// or</span>\n<span class=\"token function\">compose</span><span class=\"token punctuation\">(</span>\n  withRouter<span class=\"token punctuation\">,</span>\n  <span class=\"token function\">connect</span><span class=\"token punctuation\">(</span><span class=\"token operator\">...</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">)</span><span class=\"token punctuation\">(</span>MyComponent<span class=\"token punctuation\">)</span>\n\n<span class=\"token comment\">// This does not</span>\n<span class=\"token function\">connect</span><span class=\"token punctuation\">(</span><span class=\"token operator\">...</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">(</span><span class=\"token function\">withRouter</span><span class=\"token punctuation\">(</span>MyComponent<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span>\n<span class=\"token comment\">// nor</span>\n<span class=\"token function\">compose</span><span class=\"token punctuation\">(</span>\n  <span class=\"token function\">connect</span><span class=\"token punctuation\">(</span><span class=\"token operator\">...</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span>\n  withRouter\n<span class=\"token punctuation\">)</span><span class=\"token punctuation\">(</span>MyComponent<span class=\"token punctuation\">)</span>\n</code></pre>\n<p>See <a href=\"https://github.com/ReactTraining/react-router/blob/master/packages/react-router/docs/guides/blocked-updates.md\">this guide</a> for more information.</p>\n<h4>Static Methods and Properties</h4>\n<p>All non-react specific static methods and properties of the wrapped component are automatically copied to the\n&quot;connected&quot; component.</p>\n<h2 id=\"withRouter-componentwrappedcomponent\"><a href=\"/web/api/withRouter/componentwrappedcomponent\" class=\"internal-link\">Component.WrappedComponent</a></h2>\n<p>The wrapped component is exposed as the static property <code>WrappedComponent</code> on the returned component, which can be used\nfor testing the component in isolation, among other things.</p>\n<pre><code class=\"language-jsx\"><span class=\"token comment\">// MyComponent.js</span>\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> <span class=\"token function\">withRouter</span><span class=\"token punctuation\">(</span>MyComponent<span class=\"token punctuation\">)</span>\n\n<span class=\"token comment\">// MyComponent.test.js</span>\n<span class=\"token keyword\">import</span> MyComponent <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;./MyComponent&apos;</span>\n<span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>MyComponent.WrappedComponent</span> <span class=\"token attr-name\">location</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span></span> <span class=\"token attr-name\">...</span> <span class=\"token punctuation\">/&gt;</span></span><span class=\"token punctuation\">)</span>\n</code></pre>\n<h2 id=\"withRouter-wrappedcomponentref-func\"><a href=\"/web/api/withRouter/wrappedcomponentref-func\" class=\"internal-link\">wrappedComponentRef: func</a></h2>\n<p>A function that will be passed as the <code>ref</code> prop to the wrapped component.</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">class</span> <span class=\"token class-name\">Container</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">React<span class=\"token punctuation\">.</span>Component</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">componentDidMount</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>component<span class=\"token punctuation\">.</span><span class=\"token function\">doSomething</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">return</span> <span class=\"token punctuation\">(</span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>MyComponent</span> <span class=\"token attr-name\">wrappedComponentRef</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>c <span class=\"token operator\">=&gt;</span> <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>component <span class=\"token operator\">=</span> c<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n    <span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n","headers":[{"text":"Component.WrappedComponent","slug":"componentwrappedcomponent"},{"text":"wrappedComponentRef: func","slug":"wrappedcomponentref-func"}],"title":{"text":"withRouter","slug":"withRouter"}}

