module.exports = {"markup":"<h1 id=\"basic-components\"><a href=\"/web/guides/basic-components\" class=\"internal-link\">Basic Components</a></h1>\n<p>There are three types of components in React Router: router components, route matching components, and navigation components.</p>\n<p>All of the components that you use in a web application should be imported from <code>react-router-dom</code>.</p>\n<pre><code class=\"language-js\"><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> BrowserRouter<span class=\"token punctuation\">,</span> Route<span class=\"token punctuation\">,</span> Link <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-router-dom&apos;</span>\n</code></pre>\n<h2 id=\"basic-components-routers\"><a href=\"/web/guides/basic-components/routers\" class=\"internal-link\">Routers</a></h2>\n<p>At the core of every React Router application should be a router component. For web projects, <code>react-router-dom</code> provides <code>&lt;BrowserRouter&gt;</code> and <code>&lt;HashRouter&gt;</code> routers. Both of these will create a specialized <code>history</code> object for you. Generally speaking, you should use a <code>&lt;BrowserRouter&gt;</code> if you have a server that responds to requests and a <code>&lt;HashRouter&gt;</code> if you are using a static file server.</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> BrowserRouter <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-router-dom&apos;</span>\nReactDOM<span class=\"token punctuation\">.</span><span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>BrowserRouter</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>App</span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>BrowserRouter</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span> holder<span class=\"token punctuation\">)</span>\n</code></pre>\n<h2 id=\"basic-components-route-matching\"><a href=\"/web/guides/basic-components/route-matching\" class=\"internal-link\">Route Matching</a></h2>\n<p>There are two route matching components: <code>&lt;Route&gt;</code> and <code>&lt;Switch&gt;</code>.</p>\n<pre><code class=\"language-js\"><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> Route<span class=\"token punctuation\">,</span> Switch <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-router-dom&apos;</span>\n</code></pre>\n<p>Route matching is done by comparing a <code>&lt;Route&gt;</code>&apos;s <code>path</code> prop to the current location&#x2019;s <code>pathname</code>. When a <code>&lt;Route&gt;</code> matches it will render its content and when it does not match, it will render <code>null</code>. A <code>&lt;Route&gt;</code> with no path will always match.</p>\n<pre><code class=\"language-jsx\"><span class=\"token comment\">// when location = { pathname: &apos;/about&apos; }</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/about<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>About<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span> <span class=\"token comment\">// renders &lt;About/&gt;</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/contact<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Contact<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span> <span class=\"token comment\">// renders null</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Always<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span> <span class=\"token comment\">// renders &lt;Always/&gt;</span>\n</code></pre>\n<p>You can include a <code>&lt;Route&gt;</code> anywhere that you want to render content based on the location. It will often make sense to list a number of possible <code>&lt;Route&gt;</code>s next to each other. The <code>&lt;Switch&gt;</code> component is used to group <code>&lt;Route&gt;</code>s together.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">exact</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Home<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/about<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>About<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/contact<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Contact<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<p>The <code>&lt;Switch&gt;</code> is not required for grouping <code>&lt;Route&gt;</code>s, but it can be quite useful. A <code>&lt;Switch&gt;</code> will iterate over all of its children <code>&lt;Route&gt;</code> elements and only render the first one that matches the current location. This helps when multiple route&#x2019;s paths match the same pathname, when animating transitions between routes, and in identifying when no routes match the current location (so that you can render a &#x201C;404&#x201D; component).</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">exact</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Home<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/about<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>About<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/contact<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Contact<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token punctuation\">{</span><span class=\"token comment\">/* when none of the above match, &lt;NoMatch&gt; will be rendered */</span><span class=\"token punctuation\">}</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>NoMatch<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<h2 id=\"basic-components-route-rendering-props\"><a href=\"/web/guides/basic-components/route-rendering-props\" class=\"internal-link\">Route Rendering Props</a></h2>\n<p>You have three prop choices for how you render a component for a given <code>&lt;Route&gt;</code>: <code>component</code>, <code>render</code>, and <code>children</code>. You can check the out the <a href=\"/web/api/Route\" class=\"internal-link\"><code>&lt;Route&gt;</code> documentation</a> for more information on each one, but here we&#x2019;ll focus on <code>component</code> and <code>render</code> because those are the two you will almost always use.</p>\n<p><code>component</code> should be used when you have an existing component (either a <code>React.Component</code> or a stateless functional component) that you want to render. <code>render</code>, which takes an inline function, should only be used when you have to pass in-scope variables to the component you want to render. You should <strong>not</strong> use the <code>component</code> prop with an inline function to pass in-scope variables because you will get undesired component unmounts/remounts.</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">const</span> <span class=\"token function-variable function\">Home</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>Home<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n\n<span class=\"token keyword\">const</span> <span class=\"token function-variable function\">App</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">const</span> someVariable <span class=\"token operator\">=</span> <span class=\"token boolean\">true</span><span class=\"token punctuation\">;</span>\n  \n  <span class=\"token keyword\">return</span> <span class=\"token punctuation\">(</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token punctuation\">{</span><span class=\"token comment\">/* these are good */</span><span class=\"token punctuation\">}</span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">exact</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Home<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span>\n        <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/about<span class=\"token punctuation\">&apos;</span></span>\n        <span class=\"token attr-name\">render</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">(</span>props<span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token operator\">&lt;</span>About <span class=\"token punctuation\">{</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span>props<span class=\"token punctuation\">}</span> extra<span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>someVariable<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/&gt;</span></span><span class=\"token punctuation\">}</span>\n      <span class=\"token operator\">/</span><span class=\"token operator\">&gt;</span>\n      <span class=\"token punctuation\">{</span><span class=\"token comment\">/* do not do this */</span><span class=\"token punctuation\">}</span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span>\n        <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/contact<span class=\"token punctuation\">&apos;</span></span>\n        <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">(</span>props<span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token operator\">&lt;</span>Contact <span class=\"token punctuation\">{</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span><span class=\"token punctuation\">.</span>props<span class=\"token punctuation\">}</span> extra<span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>someVariable<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/&gt;</span></span><span class=\"token punctuation\">}</span>\n      <span class=\"token operator\">/</span><span class=\"token operator\">&gt;</span>  \n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n<h2 id=\"basic-components-navigation\"><a href=\"/web/guides/basic-components/navigation\" class=\"internal-link\">Navigation</a></h2>\n<p>React Router provides a <code>&lt;Link&gt;</code> component to create links in your application. Wherever you render a <code>&lt;Link&gt;</code>, an anchor (<code>&lt;a&gt;</code>) will be rendered in your application&#x2019;s HTML.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Link</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">&gt;</span></span>Home<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Link</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token comment\">// &lt;a href=&apos;/&apos;&gt;Home&lt;/a&gt;</span>\n</code></pre>\n<p>The <code>&lt;NavLink&gt;</code> is a special type of <code>&lt;Link&gt;</code> that can style itself as &#x201C;active&#x201D; when its <code>to</code> prop matches the current location.</p>\n<pre><code class=\"language-jsx\"><span class=\"token comment\">// location = { pathname: &apos;/react&apos; }</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>NavLink</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/react<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">activeClassName</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>hurray<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">&gt;</span></span>React<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>NavLink</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token comment\">// &lt;a href=&apos;/react&apos; className=&apos;hurray&apos;&gt;React&lt;/a&gt;</span>\n</code></pre>\n<p>Any time that you want to force navigation, you can render a <code>&lt;Redirect&gt;</code>. When a <code>&lt;Redirect&gt;</code> renders, it will navigate using its <code>to</code> prop.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Redirect</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/login<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n","headers":[{"text":"Routers","slug":"routers"},{"text":"Route Matching","slug":"route-matching"},{"text":"Route Rendering Props","slug":"route-rendering-props"},{"text":"Navigation","slug":"navigation"}],"title":{"text":"Basic Components","slug":"basic-components"}}

