module.exports = {"markup":"<h1 id=\"scroll-restoration\"><a href=\"/web/guides/scroll-restoration\" class=\"internal-link\">Scroll Restoration</a></h1>\n<p>In earlier versions of React Router we provided out-of-the-box support for scroll restoration and people have been asking for it ever since. Hopefully this document helps you get what you need out of the scroll bar and routing!</p>\n<p>Browsers are starting to handle scroll restoration with <code>history.pushState</code> on their own in the same manner they handle it with normal browser navigation. It already works in chrome and it&#x2019;s really great. <a href=\"https://majido.github.io/scroll-restoration-proposal/history-based-api.html#web-idl\">Here&#x2019;s the Scroll Restoration Spec</a>.</p>\n<p>Because browsers are starting to handle the &#x201C;default case&#x201D; and apps have varying scrolling needs (like this website!), we don&#x2019;t ship with default scroll management. This guide should help you implement whatever scrolling needs you have.</p>\n<h2 id=\"scroll-restoration-scroll-to-top\"><a href=\"/web/guides/scroll-restoration/scroll-to-top\" class=\"internal-link\">Scroll to top</a></h2>\n<p>Most of the time all you need is to &#x201C;scroll to the top&#x201D; because you have a long content page, that when navigated to, stays scrolled down. This is straightforward to handle with a <code>&lt;ScrollToTop&gt;</code> component that will scroll the window up on every navigation, make sure to wrap it in <code>withRouter</code> to give it access to the router&#x2019;s props:</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">class</span> <span class=\"token class-name\">ScrollToTop</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">Component</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">componentDidUpdate</span><span class=\"token punctuation\">(</span>prevProps<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>props<span class=\"token punctuation\">.</span>location <span class=\"token operator\">!==</span> prevProps<span class=\"token punctuation\">.</span>location<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      window<span class=\"token punctuation\">.</span><span class=\"token function\">scrollTo</span><span class=\"token punctuation\">(</span><span class=\"token number\">0</span><span class=\"token punctuation\">,</span> <span class=\"token number\">0</span><span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">return</span> <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>props<span class=\"token punctuation\">.</span>children\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> <span class=\"token function\">withRouter</span><span class=\"token punctuation\">(</span>ScrollToTop<span class=\"token punctuation\">)</span>\n</code></pre>\n<p>Then render it at the top of your app, but below Router</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">const</span> <span class=\"token function-variable function\">App</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Router</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>ScrollToTop</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>App</span><span class=\"token punctuation\">/&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>ScrollToTop</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Router</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">)</span>\n\n<span class=\"token comment\">// or just render it bare anywhere you want, but just one :)</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>ScrollToTop</span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<p>If you have a tab interface connected to the router, then you probably don&#x2019;t want to be scrolling to the top when they switch tabs. Instead, how about a <code>&lt;ScrollToTopOnMount&gt;</code> in the specific places you need it?</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">class</span> <span class=\"token class-name\">ScrollToTopOnMount</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">Component</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">componentDidMount</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    window<span class=\"token punctuation\">.</span><span class=\"token function\">scrollTo</span><span class=\"token punctuation\">(</span><span class=\"token number\">0</span><span class=\"token punctuation\">,</span> <span class=\"token number\">0</span><span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">return</span> <span class=\"token keyword\">null</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">LongContent</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">Component</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>ScrollToTopOnMount</span><span class=\"token punctuation\">/&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>h1</span><span class=\"token punctuation\">&gt;</span></span>Here is my long content page<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>h1</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token comment\">// somewhere else</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/long-content<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>LongContent<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<h2 id=\"scroll-restoration-generic-solution\"><a href=\"/web/guides/scroll-restoration/generic-solution\" class=\"internal-link\">Generic Solution</a></h2>\n<p>For a generic solution (and what browsers are starting to implement natively) we&#x2019;re talking about two things:</p>\n<ol>\n<li>Scrolling up on navigation so you don&#x2019;t start a new screen scrolled to the bottom</li>\n<li>Restoring scroll positions of the window and overflow elements on &#x201C;back&#x201D; and &#x201C;forward&#x201D; clicks (but not Link clicks!)</li>\n</ol>\n<p>At one point we were wanting to ship a generic API. Here&#x2019;s what we were headed toward:</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Router</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>ScrollRestoration</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>h1</span><span class=\"token punctuation\">&gt;</span></span>App<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>h1</span><span class=\"token punctuation\">&gt;</span></span>\n\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>RestoredScroll</span> <span class=\"token attr-name\">id</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>bunny<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">&gt;</span></span>\n        <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">style</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span> height<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;200px&apos;</span><span class=\"token punctuation\">,</span> overflow<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;auto&apos;</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">&gt;</span></span>\n          I will overflow\n        <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>RestoredScroll</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>ScrollRestoration</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Router</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<p>First, <code>ScrollRestoration</code> would scroll the window up on navigation. Second, it would use <code>location.key</code> to save the window scroll position <em>and</em> the scroll positions of <code>RestoredScroll</code> components to <code>sessionStorage</code>. Then, when <code>ScrollRestoration</code> or <code>RestoredScroll</code> components mount, they could look up their position from <code>sessionsStorage</code>.</p>\n<p>What got tricky for me was defining an &#x201C;opt-out&#x201D; API for when I didn&#x2019;t want the window scroll to be managed. For example, if you have some tab navigation floating inside the content of your page you probably <em>don&#x2019;t</em> want to scroll to the top (the tabs might be scrolled out of view!).</p>\n<p>When I learned that chrome manages scroll position for us now, and realized that different apps are going to have different scrolling needs, I kind of lost the belief that we needed to provide something&#x2013;especially when people just want to scroll to the top (which you saw is straight-forward to add to your app on your own).</p>\n<p>Based on this, we no longer feel strongly enough to do the work ourselves (like you we have limited time!). But, we&#x2019;d love to help anybody who feels inclined to implement a generic solution. A solid solution could even live in the project. Hit us up if you get started on it :)</p>\n","headers":[{"text":"Scroll to top","slug":"scroll-to-top"},{"text":"Generic Solution","slug":"generic-solution"}],"title":{"text":"Scroll Restoration","slug":"scroll-restoration"}}

